late slot=\"brand\">\n      <ds-logo></ds-logo>\n    </template>\n    <ds-menu\n      @navigate=\"$refs.page.closeDrawer()\"\n      slot=\"navbar\"\n      :routes=\"routes\"\n      navbar></ds-menu>\n    <ds-menu\n      @navigate=\"$refs.page.closeDrawer()\"\n      slot=\"drawer\"\n      :routes=\"routes\"></ds-menu>\n    <ds-page-title heading=\"Navbar only\"></ds-page-title>\n    <ds-container>\n      <ds-space margin-top=\"large\">\n        <ds-text>\n          This page uses only a navbar.\n        </ds-text>\n        <ds-text>\n          On mobile devices it will hide the navbar and show a drawer toggle.\n        </ds-text>\n      </ds-space>\n    </ds-container>\n  </ds-page>\n</template>\n\n<script>\n  export default {\n    data() {\n      return {\n        routes: [\n          { name: 'Introduction' },\n          {\n            name: 'Layout',\n            children: [\n              { name: 'Container' },\n              { name: 'Page' },\n              { name: 'Page Title' }\n            ]\n          },\n          { name: 'Typography' },\n          { name: 'Navigation' }\n        ]\n      }\n    }\n  }\n</script>\n```\n\n### Sidebar and Navbar\n\n```iframe\n<template>\n  <ds-page ref=\"page\">\n    <template slot=\"brand\">\n      <ds-logo></ds-logo>\n    </template>\n    <ds-menu\n      @navigate=\"$refs.page.closeDrawer()\"\n      slot=\"navbar\"\n      :routes=\"routes\"\n      navbar></ds-menu>\n    <ds-menu\n      @navigate=\"$refs.page.closeDrawer()\"\n      slot=\"sidebar\"\n      :routes=\"routes\"></ds-menu>\n    <ds-menu\n      @navigate=\"$refs.page.closeDrawer()\"\n      slot=\"drawer\"\n      :routes=\"routes\"></ds-menu>\n    <ds-page-title heading=\"Sidebar and Navbar\"></ds-page-title>\n    <ds-container>\n      <ds-space margin-top=\"large\">\n        <ds-text>\n          This page uses the best of both worlds.\n        </ds-text>\n        <ds-text>\n          On mobile devices it will hide the navbar as well as the sidebar and show a drawer