ovide a path to the button. You can pass a url string or a Vue router path object.\n\n```\n  <ds-button path=\"/navigation\">Click me</ds-button>\n  <ds-button :path=\"{ name: 'Navigation' }\">Click me</ds-button>\n```\n"
